.table-content, .page-content table, article.hentry table {
  width: 100%;
  border-collapse: collapse;
}

.table-content td, .page-content table td, article.hentry table td,
.table-content th,
.page-content table th,
article.hentry table th {
  font-size: 1rem;
  line-height: 1.4;
}


.table-content th,
.page-content table th,
article.hentry table th {
  position: relative;
  padding: 28px 20px;
  font-weight: 400;
  color: #073349;
  text-transform: uppercase;
  letter-spacing: 1px;
  vertical-align: middle;
  border-bottom: 2px solid #cfcfcf;
}

.table-content th::after, .page-content table th::after, article.hentry table th::after {
  position: absolute;
  bottom: 0;
  display: block;
  width: 2px;
  height: 20px;
  content: "";
  background: #cfcfcf;
}

