  color: #202020;
  vertical-align: top;
  border-bottom: 2px solid #cfcfcf;
}

@media (max-width: 767.98px) {
  /* Force table to not be like tables anymore */
  .table-content, .page-content table, article.hentry table {
    /* Hide table headers (but not display: none;, for accessibility) */
  }
  .table-content thead, .page-content table thead, article.hentry table thead,
  .table-content tbody,
  .page-content table tbody,
  article.hentry table tbody,
  .table-content th,
  .page-content table th,
  article.hentry table th, .table-content td, .page-content table td, article.hentry table td,
  .table-content tr,
  .page-content table tr,
  article.hentry table tr {
    display: block;
  }
  .table-content thead tr, .page-content table thead tr, article.hentry table thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  
  .table-content tr,
  .page-content table tr,
  article.hentry table tr {
    border-top: 2px solid #809098;
  }
  .table-content td, .page-content table td, article.hentry table td {
    position: relative;
    padding-right: 0;
    padding-left: 0;
    border-top: 1px solid #ececec;
    border-bottom: none;
  }
  .table-content td:first-child, .page-content table td:first-child, article.hentry table td:first-child {
    border-top: none;
  }
  .table-content td::before, .page-content table td::before, article.hentry table td::before {
    /* Now like a table header */
    /* Top/left values mimic padding */
    top: 6px;
    left: 6px;
    display: inline-block;
    padding-right: 0;
    margin-right: 10px;
    font-weight: 400;
    color: #073349;
    white-space: nowrap;
    content: attr(data-label) ":";
  }
}

