/* stylelint-disable */
@mixin eleganticon() {
  font-family: ElegantIcons;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  line-height: 1;
  text-transform: none;
  -webkit-font-smoothing: antialiased;
}

@font-face {
  font-family: 'ElegantIcons';
  src: url('#{$uvigoweb-font-path}/ElegantIcons.eot');
  src: url('#{$uvigoweb-font-path}/ElegantIcons.eot') format('embedded-opentype'),
    url('#{$uvigoweb-font-path}/ElegantIcons.woff') format('woff'),
    url('#{$uvigoweb-font-path}/ElegantIcons.ttf') format('truetype'),
    url('#{$uvigoweb-font-path}/ElegantIcons.svg#ElegantIcons') format('svg');
//  src: url('#{$fa-font-path}/FontAwesome.otf') format('opentype'); // used when developing fonts
  font-weight: normal;
  font-style: normal;
}

/* Use the following CSS code if you want to use data attributes for inserting your icons */
[data-icon]::before {
  content: attr(data-icon);
  font-family: ElegantIcons;
  font-variant: normal;
  font-weight: normal;
  text-transform: none;
  line-height: 1;
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
