.glyph {
  float: left;
  width: 6em;
  padding: .75em;
  margin: .4em 1.5em .75em 0;
  text-align: center;
  text-shadow: none;
}
.glyph_big {
  float: left;
  margin-right: 20px;
  font-size: 128px;
  color: #59c5dc;
}

.glyph div {
  padding-bottom: 10px;
}

.glyph input {
  width: 100%;
  padding: .2em;
  font-family: consolas, monospace;
  font-size: 12px;
  text-align: center;
  border: 0;
  box-shadow: 0 0 0 1px #ccc;
}
.centered {
  margin-right: auto;
  margin-left: auto;
}

.glyph .fs1 {
  font-size: 2em;
}
